@import "node_modules/bulmaswatch/darkly/bulmaswatch";

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: $text;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.TextEditContainer {
  height: 400px;
  overflow: scroll;
  display: flex;
  align-items: center;
  // background-color: black;
  margin-bottom: 64px;
}

.TextEdit {
  color: $text;
  text-align: left;
  margin: auto;
  // border: 2px solid $text;
  font-size: 2.5em;
}

.AudioEditContainer {
  text-align: center;
  height: 250px;
}

.BlankButton {
  border: none;
  background-color: transparent;
  color: $text;
}

.AddAudioBtn {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin: auto;
  opacity: 0.8;
}

.AddAudioBtnLabel {
  font-size: 3em;
  margin-left: 16px;
}

.UploadAudioModalFileInput {
  display: flex;
}

.UploadAudioModalFileInputData {
  margin-left: 16px;
}

.Loadable {
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.AudioControls {
  margin-top: 16px;
  display: flex;
  justify-content: center;
  align-items: center;
}

#AudioEditWaveform * {
  -ms-overflow-style: none;
}

#AudioEditWaveform *::-webkit-scrollbar {
  display: none;
}

.NavbarText {
  opacity: 0.6;
  animation-duration: 1s;
  animation-name: text-fadein;
}

@keyframes text-fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 0.6;
  }
}

.NavButton {
  opacity: 0.6;
}

.NavButton:hover {
  opacity: 1;
}

.FadeIn {
  animation-duration: 1s;
  animation-name: fadein;
  animation-play-state: paused;
}

@keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.TextViewport {
  height: 400px;
  overflow: scroll;
  color: $text;
  font-size: 2.5em;
}

.AnchorSpan {
  // border-style: solid;
  border-radius: 8px;
  font-size: 0.5em;
  // border-width: 2px;
  padding: 5px;
  background-color: $primary;
  // border-color: $light;
  // opacity: 0.7;
}

.RegionElementAnnotation {
  margin-left: 10px;
}
